<template>
  <div class="hello">
    <table class="table" v-show="videoResults">
      <tr v-for="(movie, index) in videoResults" v-bind:key="movie.id.videoId">
        <td>{{ index + 1 }}</td>
        <td>
          <a v-bind:href="'https://www.youtube.com/watch?v=' + movie.id.videoId">
            <img width="320" height="180" v-bind:src="movie.snippet.thumbnails.medium.url">
          </a>
        </td>
        <td>
          <b>{{ movie.snippet.title }}</b>
          <br>
          <span class="desc">
            {{
            movie.snippet.description
            }}
          </span>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';

export default {
  name: "SearchYoutube",
  mounted() {
          this.searchMovies()
      },
  data: function() {
    return {
      videoResults: null,
      video: {
        channelId: "", 
        part: "snippet",
        type: "video",
        order: "date",
        videoDuration: "medium",
        maxResults: "1", // 最大検索数
        key: "AIzaSyCr_do2-6YRcnGXvYf4bkmu0C2xcmsooe4"
      }
    };
  },
  props: {
    channelId: String,
    title: String
  },
  methods: {
    searchMovies: function() {
      var self = this;
      this.video.channelId=this.channelId;
      axios
        .get("https://www.googleapis.com/youtube/v3/search", {
          params: this.video
        })
        .then(function(res) {
          self.videoResults = res.data.items;
        })
    }
  }
};
</script>

<style scoped>
.desc {
  color: gray;
}
</style>